.navigation__overlay {
  display: none;
}

@media screen and (max-width: 1023px) {
  .navigation__overlay {
    display: flex;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 5;
    justify-content: space-between;
  }

  .navigation__menu {
    flex-direction: column;
    background: #202020;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 159px 90px 90px 90px;
    width: 520px;
    min-height: 100vh;
    display: flex;
    position: relative;
    align-items: center;
  }

  .navigation__overlay-container {
    flex: 1 1 auto;
  }

  .navigation__links {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 26px;
  }

  .navigation__link {
    text-decoration: none;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #fff;
    width: max-content;
    border-bottom: 2px solid transparent;
  }

  .navigation__link_active {
    border-bottom: 2px solid #fff;
    text-decoration: none;
    font-weight: 500;
    color: #fff;
    font-size: 18px;
    line-height: 22px;
  }

  .navigation__link:hover {
    opacity: 0.7;
  }

  .navigation__account-button {
    width: 100px;
    height: 32px;
    background: #202020;
    box-shadow: 0px 3px 14px rgba(0, 0, 0, 0.08);
    border-radius: 40px;
    cursor: pointer;
  }

  .navigation__account-button:hover {
    opacity: 0.6;
  }
}

@media screen and (max-width: 440px) {
  .navigation__menu {
    padding: 144px 10px 46px 10px;
    width: 100%;
  }

  .navigation__overlay-container {
    display: none;
  }

  .navigation__links {
    gap: 15px;
  }
}
